import React, {useEffect} from 'react';
import {useDispatch, useSelector} from 'react-redux';
import {postLike, deleteLike} from '../../actions/like_actions';
import {fetchPost} from '../../actions/post_actions';

export default props => {
  const currentUser = useSelector(state => state.session.user);
  const dispatch = useDispatch();

  const handleLike = () => {
    dispatch(postLike(currentUser.id, props.post.id));
    setTimeout(() => {
      dispatch(fetchPost(props.post.id));
    }, 10);
  }

  const handleUnlike = () => {
    dispatch(deleteLike(props.post.likes.filter(like => like.ownerId === currentUser.id)[0].id));
    setTimeout(() => {
      dispatch(fetchPost(props.post.id));
    }, 10);
  }

  if (!props.post) return '';
  if (props.post.likes.some(like => like.ownerId === currentUser.id)) {
    return (
      <div className='post-buttons-hover post-buttons-unlike' onClick={handleUnlike}>
        <span>
          <svg aria-hidden="true" focusable="false" data-prefix="far" data-icon="thumbs-up" className="thumbs-down-svg" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" >
            <path fill="currentColor" d="M466.27 286.69C475.04 271.84 480 256 480 236.85c0-44.015-37.218-85.58-85.82-85.58H357.7c4.92-12.81 8.85-28.13 8.85-46.54C366.55 31.936 328.86 0 271.28 0c-61.607 0-58.093 94.933-71.76 108.6-22.747 22.747-49.615 66.447-68.76 83.4H32c-17.673 0-32 14.327-32 32v240c0 17.673 14.327 32 32 32h64c14.893 0 27.408-10.174 30.978-23.95 44.509 1.001 75.06 39.94 177.802 39.94 7.22 0 15.22.01 22.22.01 77.117 0 111.986-39.423 112.94-95.33 13.319-18.425 20.299-43.122 17.34-66.99 9.854-18.452 13.664-40.343 8.99-62.99zm-61.75 53.83c12.56 21.13 1.26 49.41-13.94 57.57 7.7 48.78-17.608 65.9-53.12 65.9h-37.82c-71.639 0-118.029-37.82-171.64-37.82V240h10.92c28.36 0 67.98-70.89 94.54-97.46 28.36-28.36 18.91-75.63 37.82-94.54 47.27 0 47.27 32.98 47.27 56.73 0 39.17-28.36 56.72-28.36 94.54h103.99c21.11 0 37.73 18.91 37.82 37.82.09 18.9-12.82 37.81-22.27 37.81 13.489 14.555 16.371 45.236-5.21 65.62zM88 432c0 13.255-10.745 24-24 24s-24-10.745-24-24 10.745-24 24-24 24 10.745 24 24z"></path>
          </svg>
            <p>Unlike</p>
        </span>
      </div>
    )
  } else {
    return (
      <div className='post-buttons-hover post-buttons-like' onClick={handleLike}>
        <span>
          <svg aria-hidden="true" focusable="false" data-prefix="far" data-icon="thumbs-up" className="thumbs-up-svg" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512"><path fill="currentColor" d="M466.27 286.69C475.04 271.84 480 256 480 236.85c0-44.015-37.218-85.58-85.82-85.58H357.7c4.92-12.81 8.85-28.13 8.85-46.54C366.55 31.936 328.86 0 271.28 0c-61.607 0-58.093 94.933-71.76 108.6-22.747 22.747-49.615 66.447-68.76 83.4H32c-17.673 0-32 14.327-32 32v240c0 17.673 14.327 32 32 32h64c14.893 0 27.408-10.174 30.978-23.95 44.509 1.001 75.06 39.94 177.802 39.94 7.22 0 15.22.01 22.22.01 77.117 0 111.986-39.423 112.94-95.33 13.319-18.425 20.299-43.122 17.34-66.99 9.854-18.452 13.664-40.343 8.99-62.99zm-61.75 53.83c12.56 21.13 1.26 49.41-13.94 57.57 7.7 48.78-17.608 65.9-53.12 65.9h-37.82c-71.639 0-118.029-37.82-171.64-37.82V240h10.92c28.36 0 67.98-70.89 94.54-97.46 28.36-28.36 18.91-75.63 37.82-94.54 47.27 0 47.27 32.98 47.27 56.73 0 39.17-28.36 56.72-28.36 94.54h103.99c21.11 0 37.73 18.91 37.82 37.82.09 18.9-12.82 37.81-22.27 37.81 13.489 14.555 16.371 45.236-5.21 65.62zM88 432c0 13.255-10.745 24-24 24s-24-10.745-24-24 10.745-24 24-24 24 10.745 24 24z"></path></svg>
            <p>Like</p>
        </span>
      </div>
    )
  }

}
